body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
}


nav .brand-logo, nav a, .button-collapse {
  color: black;
}

  /* label color */
  .input-field label {
      color: #000;
    }
    /* label focus color */
    .input-field input[type=text]:focus + label {
      color: #000;
    }
    /* label underline focus color */
    .input-field input[type=text]:focus {
      border-bottom: 1px solid #000;
      -webkit-box-shadow: 0 1px 0 0 #000;
              box-shadow: 0 1px 0 0 #000;
    }
    /* valid color */
    .input-field input[type=text].valid {
      border-bottom: 1px solid #000;
      -webkit-box-shadow: 0 1px 0 0 #000;
              box-shadow: 0 1px 0 0 #000;
    }
    /* invalid color */
    .input-field input[type=text].invalid {
      border-bottom: 1px solid #000;
      -webkit-box-shadow: 0 1px 0 0 rgb(114, 77, 77);
              box-shadow: 0 1px 0 0 rgb(114, 77, 77);
    }
    /* icon prefix focus color */
    .input-field .prefix.active {
      color: #000;
    }

    #profpic {
      margin: 1%
    }

    #form-wrapper {
      width: 300px;
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -200px;
      margin-left: -150px;
      background: white;
      color: #05668D;
      padding: 50px
    }

    #form-wrapper a, #headline {
      color: #05668D
    }

    #marbot {
      margin-bottom: 1%;
    }

    .marright {
      margin-right: 35%;
    }

    .marpush {
      margin-left: 15%;
    }

    .marleft {
      margin-left: 2%
    }


    .targetpic {
      width: 100px;
      height: 100px;
     
    }

    a {
      color: white;
    }
    a:hover, .card-title:hover {
        color: greenyellow;
        text-decoration: underline;
    }


